import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import Index from '../views/Index'
import BarView from '../views/BarView'
import LineView from '../views/LineView'
import PieView from "../views/PieView"
import MapView from "../views/MapView"
import DetailIndex from "../views/DetailIndex"

export default new Router({
    mode: 'history',
    routes: [
        { path: '/index', name:"index",component:Index,
        	children: [
        		{path:'bar',name:"bar",component:BarView},
        		{path:'line',name:"line",component:LineView},
        		{path:'pie',name:"pie",component:PieView},
        		{path:'map',name:"map",component:MapView}
        	]
        },
        {
        	path:'/detailIndex/:id',name:"detailIndex",component:DetailIndex
        },
        {
        	path:'*',redirect:'/index/bar'
        }
    ]
})






